import React, { Component } from 'react';
import { Button, Dropdown } from 'react-bootstrap';
import '../bootstrap/dist/css/bootstrap.min.css';

class FooComponent extends Component {
    constructor(props) {
        super(props);
        this.switchFlag = this.switchFlag.bind(this);
        this.state = {
            styleFlag: false,
        }
    }

    switchFlag = _ => {
        this.setState({
            styleFlag: !this.state.styleFlag
        })
    };

    render() {

        const backFunction = _ => {
            if (this.state.styleFlag) {
                return 'orange';
            }
            return 'yellow';
        };

        let css = {
            fontSize: this.state.styleFlag ? 32 : 24,
            background: backFunction()
        }

        return (
            <div>
                <p style={css}> example </p>
                <Button variant="primary" onClick={this.switchFlag}>Primary</Button>{' '}
                <Button variant="secondary">Secondary</Button>{' '}
                <Button variant="success">Success</Button>{' '}
                <Button variant="warning">Warning</Button>{' '}
                <Button variant="danger">Danger</Button> <Button variant="info">Info</Button>{' '}
                <Button variant="light">Light</Button> <Button variant="dark">Dark</Button>{' '}
                <Button variant="link">Link</Button>
                <Dropdown>
                    <Dropdown.Toggle variant="success" id="dropdown-basic">
                        Dropdown Button1
                </Dropdown.Toggle>

                    <Dropdown.Menu>
                        <Dropdown.Item href="#/action-1">Action</Dropdown.Item>
                        <Dropdown.Item href="#/action-2">Another action</Dropdown.Item>
                        <Dropdown.Item href="#/action-3">Something else</Dropdown.Item>
                    </Dropdown.Menu>
                </Dropdown>
            </div>
        )
    }
}

export default FooComponent;